import React from 'react';
import { AriaCalendarProps, useCalendar, useLocale } from 'react-aria';
import { useCalendarState } from 'react-stately';
import { createCalendar } from '@internationalized/date';
import type { DateValue } from '@react-types/calendar';
import classNames from 'classnames';

import SvgCaretLeft from '../Icons/CaretLeft';
import SvgCaretRight from '../Icons/CaretRight';

import CalendarButton from './CalendarButton';
import CalendarGrid from './CalendarGrid';

import styles from './Calendar.module.scss';

export interface CalendarProps extends AriaCalendarProps<DateValue> {
    className?: string;
}

const Calendar = (props: CalendarProps) => {
    const { locale } = useLocale();
    const { className, errorMessage, validationState, isDisabled } = props;
    const state = useCalendarState({
        ...props,
        locale,
        createCalendar,
    });
    const {
        calendarProps,
        prevButtonProps,
        nextButtonProps,
        title,
        errorMessageProps,
    } = useCalendar(props, state);

    const classList = classNames(styles.calendar, className, {
        [styles[`calendar--invalid`]]: validationState === 'invalid',
        [styles[`calendar--disabled`]]: isDisabled,
    });

    return (
        <div {...calendarProps} className={classList}>
            <div className={styles.calendar__header}>
                <div className={styles.calendar__wrapper}>
                    <CalendarButton {...prevButtonProps}>
                        <SvgCaretLeft aria-hidden="true" />
                    </CalendarButton>
                    <h2 className={styles.calendar__title}>{title}</h2>
                    <CalendarButton {...nextButtonProps}>
                        <SvgCaretRight aria-hidden="true" />
                    </CalendarButton>
                </div>
            </div>
            <CalendarGrid state={state} />
            {errorMessage && (
                <div {...errorMessageProps} className={styles.calendar__error}>
                    {errorMessage}
                </div>
            )}
        </div>
    );
};

export default Calendar;
