import React, { useRef } from 'react';
import {
    AriaCalendarCellProps,
    mergeProps,
    useCalendarCell,
    useFocusRing,
} from 'react-aria';
import { CalendarState } from 'react-stately';
import classNames from 'classnames';

import styles from './Calendar.module.scss';

interface CalendarCellProps extends AriaCalendarCellProps {
    state: CalendarState;
    gaName?: string;
}

const CalendarCell = ({ state, date }: CalendarCellProps) => {
    const ref = useRef(null);
    const {
        cellProps,
        buttonProps,
        isSelected,

        isOutsideVisibleRange,
        isDisabled,
        isUnavailable,
        formattedDate,
    } = useCalendarCell({ date }, state, ref);
    const { isFocusVisible, focusProps } = useFocusRing();

    const classList = classNames(styles.calendar__cell, {
        [styles[`calendar__cell--selected`]]: isSelected,
        [styles[`calendar__cell--unavailable`]]: isUnavailable,
        [styles[`calendar__cell--disabled`]]: isDisabled,
        [styles[`focus-ring`]]: isFocusVisible,
    });

    return (
        <td {...cellProps}>
            <div
                {...mergeProps(buttonProps, focusProps)}
                className={classList}
                ref={ref}
                hidden={isOutsideVisibleRange}
            >
                {formattedDate}
            </div>
        </td>
    );
};

export default CalendarCell;
